import React from "react";
import localFont from 'next/font/local';

// Подключаем шрифт прямо в компоненте
const nothingFont = localFont({
  src: [
    {
      path: '../../public/fonts/Nothing/Nothing.ttf',
      weight: '400',
      style: 'normal',
    },
   
  ],
  variable: '--font-nothing'
});

const Header = () => {
  return (
    <header 
      className={`flex justify-between  text-[#FFF] items-start font-sans`}
    >
      <a href="#">
        <span className="text-[1.25vw] text-[#FFF] ppmori">GARAGE AGENCY</span>
      </a>
      
      <div className="font-nothing text-[1.25vw] ppmori">a new era of tech <br /> excellence</div>
      
      <div className="flex flex-col font-nothing">
        <a href="#" className="text-[#fff] text-[1.25vw]"><span className="mr-[0.38vw]">{'>'}</span> <span className=" ppmori">WEB-DEV</span></a>
        <a href="#" className="text-[#fff] text-[1.25vw]"><span className="mr-[0.38vw]">{'>'}</span> <span className=" ppmori">DESIGN</span></a>
        <a href="#" className="text-[#fff] text-[1.25vw]"><span className="mr-[0.38vw]">{'>'}</span> <span className=" ppmori">PACKAGE</span></a>
        <a href="#" className="text-[#fff] text-[1.25vw]"><span className="mr-[0.38vw]">{'>'}</span> <span className=" ppmori">MARKETING</span></a>
        <a href="#" className="text-[#fff] text-[1.25vw]"><span className="mr-[0.38vw]">{'>'}</span> <span className=" ppmori">SEO</span></a>
        {/* Остальные ссылки... */}
      </div>
      
      <div>
        <a href="#" className="text-[1vw] text-[#212121] bg-[#fff] py-[0.63vw] px-[2.75vw] rounded-[0.63vw]  ppmori">
          Let’s talk
        </a>
      </div>
    </header>
  );
};

export default Header;